
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}


.row{
  margin: 10px 0;
  input[type="text"],
  select,
  textarea{
    width: 100%;
    box-shadow: 0 0 5px #bbb;
    -o-box-shadow: 0 0 5px #bbb;
    -moz-box-shadow: 0 0 5px #bbb;
    -webkit-box-shadow: 0 0 5px #bbb;
    border-radius: 5px;
  } 
  select{
    padding: 10px;
    background-color: #f2f2f2;
    border-color: #aaa;
  }
  input[type="submit"]{
    margin-top: 30px;
  }
}

.list_row{
  background-color: grayscale($color: #b1adad54);
  display: flex;
  margin: 10px 0px;
  >div{
    &:first-child{
      flex: 10;
      padding: 10px;
    }
    &.details
    {
      flex: 1;
      align-items: center;
      justify-content: center;
      display: flex;
      background-color: rgba(0,0,0,0.2);
      padding: 10px;
      &:hover{
        background-color: rgba(0,0,0,0.3);
      }
      a{
        text-decoration: none;
        line-height: initial;
        color: darkslategray;
        span{
          margin-right: 8px;
        }
      }
    }
    &.nvote,
    &.vote{
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &:hover{
        background-color: rgba(0,0,0,0.25);
      }
      span{
        width: 25px;
        height: 25px;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        &.votespan{
          display: block;
        }
        &.nagativevote{
          display: block;
          transform: rotate(180deg);
          -o-transform: rotate(180deg);
          -webkit-transform: rotate(180deg);
          -moz-transform: rotate(180deg);
        }
        &.vote_count{
          width: 15px;
        }
      }
    }
  }
}
:global(#comments){
  position: relative;
  .loader{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
   >div{
      position:absolute;
      width: 100%;
      height: 100%;
      display: flex;
      background-color: #a9a9a96b;
      align-items: center;
      justify-content: center;
      left: 0;
      top: 0;
      img{
        width: 100px;
      }
   }
  }
}


.commentlists{
  .row{
      margin: 10px 0;
      display: flex;
      background-color: antiquewhite;
      >div{
        &:first-child{
          flex: 2;
          text-align: center;
          background-color: aliceblue;
          padding: 20px;
          img{
            max-width: 100%;
            border-radius: 100px;
            width: 60px;
            margin: 0 auto;
          }
          p{
            margin: 0;
          }
        }
        &:last-child{
          flex: 10;
          padding: 20px;
          position: relative;
          .nvote{
            width: 52px;
          }
          .vote{
            width: 25px;
          }
          .nvote,
          .vote{
            position: absolute;
            top: 07px;
            right: 07px;
            
            cursor: pointer;
            height: 25px;
            opacity: 0.5;
            display: flex;
            &:hover{
              opacity: 1;
            }
            span{
              height: 100%;
              display: block;
              &.nagativevote{
                transform: rotate(180deg);
              }
              &:first-child{
                text-align: center;
                width: calc(100% - 25px);
              }
              &:last-child{
                background-position: center;
                background-size: contain;
                width: 25px;
              }
            }
          }
        }
      }
    }
}